@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";

.cathay-toolbar{
	position:fixed;
	bottom:0;
	left:0;
	width: 100%;
	z-index: 200;
    box-shadow: 0px -3px 6px -1px rgba(0,0,0,0.2);


  	transition: transform .2s;
	transform:translateY(10px);

	.safari-addressbar-off &{
  		transition: transform .2s;
		transform:translateY(0px);
	}

	.toolbar-on & {
		z-index: 1001;
	}
	
	@media (min-width: map-get($grid-breakpoints, 'lg')) {
		display:none;
	}

    &:before{
    	pointer-events: none;
    	content:'';
    	position:absolute;
    	display:block;
    	width:70px;
    	height:70px;
    	border-radius: 50%;
    	background-color:#fff;
    	top:-15px;
    	left:0;
    	right:0;
    	margin-left:auto;
    	margin-right:auto;
    	box-shadow: 0px -3px 6px -1px rgba(0,0,0,0.2);
    }

    &:after{
    	pointer-events: none;
    	content:'';
    	position:absolute;
    	display:block;
    	width:80px;
    	height:70px;
    	background-color:#fff;
    	top:0px;
    	left:0;
    	right:0;
    	margin-left:auto;
    	margin-right:auto;
    }

	.ct-img{
		display:block;
		margin:0 auto 4px;
		width:23px;
		height:28px;
		overflow:hidden;
		background-size:100%;
		background-position: 0 0;
	}

	.ct-link{
		color:#777777;
		line-height:1;
		font-size:13px;
		display:block;
		&:hover{
			color:#777777;
		}
	}
	
	p{
		margin-top:0px;
		margin-bottom:0;
		line-height:1;
	}

    .ct-menu{
		display:flex;
	    flex-wrap: nowrap;
		width: 100%;
		background-color:#fff;
		padding:5px 0 20px;
    	position: relative;
    	z-index:1;
    }
 
    .ct-menu-item{
		flex:1 1 20%;
		text-align:center;
		position:relative;
    }

	.ct-menu-item--master{
		.ct-img{
			margin-top:-5px;
			margin-bottom:9px;
		}
		.ct-link{
			p{
				color:#00a94f;
			}
			// .ct-img{
			// 	background-position: 0 100%;
			// }
		}
	}
	.ct-list{
		display:none;
		position:fixed;
		bottom:0;
		left:0;
		width:100%;
		z-index: 1002;
	}


	.ct-list-link{
		color:#fff;
		line-height:1;
		font-size:15px;
		display:block;
		p{
			margin-top:5px;
		}

		&:hover{
			color:#fff;
			.ct-list-img{
				background-color:rgba(0,118,55,1);
			}
		}
	}
	.ct-list-img{
		background-color:rgba(0,169,79,1);
		width:50px;
		height:50px;
		display:block;
		margin:0 auto;
		overflow:hidden;
		border-radius:50%;

		$ratio:0.25;
		background-size: round(124px * $ratio) round(130px * $ratio);
	    background-position: center center;
	    background-repeat: no-repeat;
	}

	.ct-list-item{
		width: 20%;
		position:fixed;
		left:40%;
		bottom:0;
		opacity:0;
		pointer-events: none;
		transition: all .25s ease-in-out;

		&:nth-child(1){
			transition-delay: 0s;
		}
		&:nth-child(2){
			transition-delay: 0s;
		}
		&:nth-child(3){
			transition-delay: 0s;
		}
		&:nth-child(4){
			transition-delay: 0s;
		}
		&:nth-child(5){
			transition-delay: 0s;
		}

		&.ct-list-item--close{
			.ct-list-link{
				transition-delay: 0.3s;
				border-radius:50%;
				border:2px solid #fff;
				background-color:rgba(0,0,0,0);
				width:35px;
				height:35px;
				display:block;
				margin:0 auto;
				overflow:hidden;
				border-radius:50%;
				position: relative;
				&:before, &:after {
					position: absolute;
					top: 0;
					bottom: 0;
					left: 0;
					right: 0;
					margin: auto;
					content: ' ';
					height: 20px;
					width: 2px;
					background-color: #fff;
				}
				&:before {
				  transform: rotate(45deg);
				}
				&:after {
				  transform: rotate(-45deg);
				}
			}

		}
		.toolbar-show & {

			opacity:1;
			pointer-events: auto;
			$basic_bottom:60px;

			// &:nth-child(1){
			// 	transition-delay: 0s;
			// }
			// &:nth-child(2){
			// 	transition-delay: 0.1s;
			// }
			// &:nth-child(3){
			// 	transition-delay: 0.15s;
			// }
			// &:nth-child(4){
			// 	transition-delay: 0.2s;
			// }
			// &:nth-child(5){
			// 	transition-delay: 0.25s;
			// }
			&:nth-child(1){
				bottom:$basic_bottom + 30px;
				left:0%;
			}
			&:nth-child(2){
				bottom:$basic_bottom + 60px;
				left:20%;
			}
			&:nth-child(3){
				bottom:$basic_bottom + 90px;
				left:40%;
			}
			&:nth-child(4){
				bottom:$basic_bottom + 60px;
				left:60%;
			}
			&:nth-child(5){
				bottom:$basic_bottom + 30px;
				left:80%;
			}
			&.ct-list-item--close{
				bottom:$basic_bottom + 40px;
				left:40%;
			}
		}
	}
}